// stories/o-callout.stories.tsx
import type { Decorator, Meta, StoryObj } from '@storybook/web-components';
import { html } from 'lit';
import type { OCallout } from '../components/o-callout/o-callout';

export default {
	title: 'Components/Callout',
	argTypes: {},
	args: {}
} as Meta;

// Custom padding to increase padding for a Button Full Width
const customPaddingDecorator: Decorator = (story) => {
	return html`<div style="padding: 30px 20px;">${story()}</div>`;
};

const TemplateDefault: StoryObj<OCallout> = {
	parameters: {
		layout: 'fullscreen'
	},
	render: () => html` <o-callout> Place content here </o-callout>`
};

export const Default: StoryObj = {
	...TemplateDefault,
	decorators: [customPaddingDecorator],
	args: {},
	parameters: {
		layout: 'fullscreen',
		docs: {
			source: {
				transform: (_, storyContext) => {
					const {} = storyContext;
					//Create Dynamic code example using args
					return `
-----------------------
// Vue Code Snippet
-----------------------

import { OCallout } from "@oui/vue";

<o-callout><slot></slot></o-callout>

-----------------------
// React Code Snippet
-----------------------

import { OCallout } from "@oui/react";

<OCallout><slot></slot></OCallout>
        `;
				}
			}
		}
	}
};
